
import React, { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleLeft, faEnvelope, faUnlockAlt } from "@fortawesome/free-solid-svg-icons";
import { faFacebookF, faGithub, faTwitter } from "@fortawesome/free-brands-svg-icons";
import { Col, Row, Form, Card, Button, FormCheck, Container, InputGroup } from '@themesberg/react-bootstrap';
import { Link, navigate } from 'gatsby';
import useAuth from "../hooks/userAuth";
import BgImage from "../assets/img/illustrations/signin.svg";
import { StaticImage } from "gatsby-plugin-image"
import Logo from "../components/Logo"
export default () => {

  const { login } = useAuth()
  const [message, setMessage] = useState("")
  const [payload, setPayload] = useState({
    username: "",
    password: ""
  })

  const handleTextField = (e, payloadKey) => {
    setPayload({
      ...payload,
      [payloadKey]: e.target.value
    })
  }
  const handleBtn = async () => {
    try {
      const res = await login(payload.username, payload.password).then(e => navigate('/'))
    } catch (e) {
      setMessage(e.message)
    }
  }

  return (
    <main>
      <section className="d-flex align-items-center my-5 mt-lg-6 mb-lg-5">
        <Container>
          <Row className="justify-content-center form-bg-image" style={{ backgroundImage: `url(${BgImage})` }}>
            <Col xs={12} className="d-flex align-items-center justify-content-center">
              <div className="bg-white shadow-soft border rounded border-light p-4 p-lg-5 w-100 fmxw-500">
                <div className="text-center text-md-center mb-4 mt-md-0">
                  <Logo />
                  <h3 className="mb-0">Sign in to our platform</h3>
                </div>
                <Form className="mt-4">

                  <Form.Group id="email" className="mb-4">
                    <Form.Label>Your Email</Form.Label>
                    <InputGroup>
                      <InputGroup.Text>
                        <FontAwesomeIcon icon={faEnvelope} />
                      </InputGroup.Text>
                      <Form.Control value={payload.username} onChange={(e) => handleTextField(e, 'username')} autoFocus required type="email" placeholder="example@company.com" />
                    </InputGroup>
                  </Form.Group>
                  <Form.Group>
                    <Form.Group id="password" className="mb-4">
                      <Form.Label>Your Password</Form.Label>
                      <InputGroup>
                        <InputGroup.Text>
                          <FontAwesomeIcon icon={faUnlockAlt} />
                        </InputGroup.Text>
                        <Form.Control value={payload.password} onChange={(e) => handleTextField(e, 'password')} required type="password" placeholder="Password" />
                      </InputGroup>
                    </Form.Group>
                    <div className="d-flex justify-content-between align-items-center mb-4">

                      <Card.Link as={Link} to="/resetPassword" className="small text-end">Reset password?</Card.Link>
                    </div>
                  </Form.Group>
                  <Button variant="primary" className="w-100" onClick={handleBtn}>
                    Sign in
                  </Button>
                  <div className="text-center" style={{ color: 'red' }}>
                    {
                      message
                    }
                  </div>
                </Form>
                <div className="d-flex justify-content-center align-items-center mt-4">
                  <span className="fw-normal">
                    Not registered?
                    <Card.Link as={Link} to="/register" className="fw-bold">
                      {` Create account `}
                    </Card.Link>
                  </span>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section >
    </main >
  );
};
